.extra {
  padding-top: 25px;
  box-sizing: border-box;
  padding-bottom: 35px;
  display: flex;
  flex-direction: column;
}

.extra__title {
  @include title-style-none;
  font-weight: normal;
  margin-bottom: 31px;
  position: relative;
  width: auto;
  display: flex;
  align-self: flex-start;

  &::after {
    content: "";
    position: absolute;
    width: 100vw;
    transform: translate(100%);
    height: 3px;
    top: 17px;
    right: -22px;
    background-color: #68b738;
  }
}

.extra__list {
  @include list-style-none;
}

.extra__item {
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 15px;
}

.extra__gift {
  margin-top: 41px;
}

@media (min-width: $tablet-min-weight) {
  .extra {
    padding-top: 65px;
    box-sizing: border-box;
    padding-bottom: 76px;
    position: relative;
  }

  .extra__title {
    font-size: 36px;
    line-height: 36px;
    margin-left: 5.17%;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 100vw;
      transform: translate(-100%);
      height: 3px;
      top: 21px;
      left: -40px;
      background-color: #68b738;
    }

    &::after {
      content: "";
      top: 21px;
      right: -35px;
    }
  }

  .extra__list {
    padding-top: 48px;
  }

  .extra__item {
    padding-top: 25px;
    padding-bottom: 27px;
  }

  .extra__gift {
    margin-top: 69px;
  }
}

@media (min-width: $desctop-min-weight) {
  .extra {
    padding-top: 69px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 87px;
  }

  .extra__list {
    width: 73.4%;
    padding-top: 46px;
  }

  .extra__title {
    width: auto;
    margin-left: 0;
  }

  .extra__gift {
    width: 20.1%;
    margin-top: 48px;
  }

  .extra__item {
    padding-top: 16px;
    padding-bottom: 9px;
  }
}
