.main-navigation {
  background-color: #ffffff;
  padding-top: 19px;
  min-width: 100%;
}

.main-navigation__list--hidden {
  display: none;
}

@media (min-width: $tablet-min-weight) {
  .main-navigation {
    min-width: 300px;
    padding-top: 0;
  }

  .main-navigation__list--hidden {
    display: flex;
  }
}

@media (min-width: $desctop-min-weight) {
  .main-navigation {
    width: 50%;
    background-color: transparent;
    box-sizing: border-box;
    padding-left: 66px;
  }
}
